import { useAuth } from "@/contexts/AuthContext";
import { useUserRole } from "@/hooks/useUserRole";
import { useMyActivityRequests, useResubmitActivityRequest, useDeleteActivityRequest } from "@/hooks/useActivityRequests";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { AlertDialog, AlertDialogAction, AlertDialogCancel, AlertDialogContent, AlertDialogDescription, AlertDialogFooter, AlertDialogHeader, AlertDialogTitle, AlertDialogTrigger } from "@/components/ui/alert-dialog";
import { Plus, Loader2, Calendar, Clock, Edit, RefreshCw, Trash2, ExternalLink, AlertTriangle } from "lucide-react";
import { Link, useNavigate } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { useLocalizedContent } from "@/hooks/useLocalizedContent";
import { format } from "date-fns";

const MerchantActivityRequests = () => {
  const { user } = useAuth();
  const navigate = useNavigate();
  const { t } = useTranslation();
  const { isMerchant, isSupervisor, isLoading: roleLoading } = useUserRole();
  const { data: requests = [], isLoading } = useMyActivityRequests();
  const { localizeActivity } = useLocalizedContent();
  const resubmitRequest = useResubmitActivityRequest();
  const deleteRequest = useDeleteActivityRequest();

  // Redirect if not merchant or supervisor
  if (!roleLoading && !isMerchant && !isSupervisor) {
    navigate("/");
    return null;
  }

  if (isLoading || roleLoading) {
    return (
      <div className="flex justify-center items-center min-h-[50vh]">
        <Loader2 className="w-8 h-8 animate-spin text-primary" />
      </div>
    );
  }

  const getStatusBadge = (request: typeof requests[0]) => {
    // If activity was approved but then deleted
    if (request.status === "approved" && request.activity_deleted) {
      return (
        <Badge variant="outline" className="border-orange-500 text-orange-500 flex items-center gap-1">
          <AlertTriangle className="w-3 h-3" />
          {t("merchant.activityDeleted")}
        </Badge>
      );
    }
    
    switch (request.status) {
      case "approved":
        return <Badge className="bg-green-500">{t("merchant.statusApproved")}</Badge>;
      case "rejected":
        return <Badge variant="destructive">{t("merchant.statusRejected")}</Badge>;
      default:
        return <Badge variant="secondary">{t("merchant.statusPending")}</Badge>;
    }
  };

  const handleResubmit = (requestId: string) => {
    resubmitRequest.mutate(requestId);
  };

  const handleDelete = (requestId: string) => {
    deleteRequest.mutate(requestId);
  };

  return (
    <div>
      <div className="flex justify-between items-center mb-8">
        <div>
          <h1 className="text-3xl font-bold mb-2">{t("merchant.myActivityRequests")}</h1>
          <p className="text-muted-foreground">
            {t("merchant.viewManageRequests")}
          </p>
        </div>
        <Button asChild>
          <Link to="/merchant/activity-request-form">
            <Plus className="w-4 h-4 mr-2" />
            {t("merchant.createRequest")}
          </Link>
        </Button>
      </div>

      {requests.length === 0 ? (
        <Card>
          <CardContent className="py-12 text-center">
            <p className="text-muted-foreground mb-4">{t("merchant.noRequests")}</p>
            <Button asChild>
              <Link to="/merchant/activity-request-form">
                <Plus className="w-4 h-4 mr-2" />
                {t("merchant.createRequest")}
              </Link>
            </Button>
          </CardContent>
        </Card>
      ) : (
        <div className="grid grid-cols-1 gap-6">
          {requests.map((request) => {
            const localized = localizeActivity(request);
            const isDeleted = request.status === "approved" && request.activity_deleted;
            const isPending = request.status === "pending";
            const isRejected = request.status === "rejected";
            
            return (
              <Card key={request.id} className={isDeleted ? "border-orange-200 bg-orange-50/50 dark:border-orange-900 dark:bg-orange-950/20" : ""}>
                <CardHeader>
                  <div className="flex items-start justify-between gap-4">
                    <CardTitle className="flex-1">{localized.title}</CardTitle>
                    {getStatusBadge(request)}
                  </div>
                </CardHeader>
                <CardContent className="space-y-4">
                  {request.image_url && (
                    <div className="relative h-48 rounded-lg overflow-hidden">
                      <img
                        src={request.image_url}
                        alt={localized.title}
                        className="w-full h-full object-cover"
                      />
                    </div>
                  )}
                  
                  {localized.intro && (
                    <p className="text-muted-foreground">{localized.intro}</p>
                  )}

                  <div className="flex flex-wrap gap-4 text-sm text-muted-foreground">
                    <div className="flex items-center gap-2">
                      <Calendar className="w-4 h-4" />
                      <span>
                        {format(new Date(request.start_date), "yyyy-MM-dd")}
                        {request.end_date && ` - ${format(new Date(request.end_date), "yyyy-MM-dd")}`}
                      </span>
                    </div>
                    <div className="flex items-center gap-2">
                      <Clock className="w-4 h-4" />
                      <span>
                        {t("merchant.submittedAt")} {format(new Date(request.created_at), "yyyy-MM-dd HH:mm")}
                      </span>
                    </div>
                  </div>

                  {request.admin_notes && (
                    <div className="p-4 bg-muted rounded-lg">
                      <p className="text-sm font-medium mb-1">{t("merchant.adminNotes")}</p>
                      <p className="text-sm text-muted-foreground">{request.admin_notes}</p>
                    </div>
                  )}

                  {/* Action buttons based on status */}
                  <div className="flex flex-wrap gap-2 pt-2">
                    {/* Show "View Activity" only if approved and NOT deleted */}
                    {request.status === "approved" && !request.activity_deleted && request.created_activity_id && (
                      <Button asChild variant="outline" className="flex-1 sm:flex-none">
                        <Link to={`/activity/${request.created_activity_id}`}>
                          <ExternalLink className="w-4 h-4 mr-2" />
                          {t("merchant.viewCreatedActivity")}
                        </Link>
                      </Button>
                    )}

                    {/* Show edit and resubmit for deleted activities */}
                    {isDeleted && (
                      <>
                        <Button 
                          asChild 
                          variant="outline"
                          className="flex-1 sm:flex-none"
                        >
                          <Link to={`/merchant/activity-request-form?edit=${request.id}`}>
                            <Edit className="w-4 h-4 mr-2" />
                            {t("merchant.edit")}
                          </Link>
                        </Button>
                        <Button
                          variant="default"
                          className="flex-1 sm:flex-none"
                          onClick={() => handleResubmit(request.id)}
                          disabled={resubmitRequest.isPending}
                        >
                          {resubmitRequest.isPending ? (
                            <Loader2 className="w-4 h-4 mr-2 animate-spin" />
                          ) : (
                            <RefreshCw className="w-4 h-4 mr-2" />
                          )}
                          {t("merchant.resubmit")}
                        </Button>
                      </>
                    )}

                    {/* Show edit for pending requests */}
                    {isPending && (
                      <Button 
                        asChild 
                        variant="outline"
                        className="flex-1 sm:flex-none"
                      >
                        <Link to={`/merchant/activity-request-form?edit=${request.id}`}>
                          <Edit className="w-4 h-4 mr-2" />
                          {t("merchant.edit")}
                        </Link>
                      </Button>
                    )}

                    {/* Show resubmit for rejected requests */}
                    {isRejected && (
                      <>
                        <Button 
                          asChild 
                          variant="outline"
                          className="flex-1 sm:flex-none"
                        >
                          <Link to={`/merchant/activity-request-form?edit=${request.id}`}>
                            <Edit className="w-4 h-4 mr-2" />
                            {t("merchant.edit")}
                          </Link>
                        </Button>
                        <Button
                          variant="default"
                          className="flex-1 sm:flex-none"
                          onClick={() => handleResubmit(request.id)}
                          disabled={resubmitRequest.isPending}
                        >
                          {resubmitRequest.isPending ? (
                            <Loader2 className="w-4 h-4 mr-2 animate-spin" />
                          ) : (
                            <RefreshCw className="w-4 h-4 mr-2" />
                          )}
                          {t("merchant.resubmit")}
                        </Button>
                      </>
                    )}

                    {/* Delete button for pending, rejected, or deleted activities */}
                    {(isPending || isRejected || isDeleted) && (
                      <AlertDialog>
                        <AlertDialogTrigger asChild>
                          <Button variant="destructive" size="icon" className="shrink-0">
                            <Trash2 className="w-4 h-4" />
                          </Button>
                        </AlertDialogTrigger>
                        <AlertDialogContent>
                          <AlertDialogHeader>
                            <AlertDialogTitle>{t("merchant.confirmDelete")}</AlertDialogTitle>
                            <AlertDialogDescription>
                              {t("merchant.confirmDeleteDesc")}
                            </AlertDialogDescription>
                          </AlertDialogHeader>
                          <AlertDialogFooter>
                            <AlertDialogCancel>{t("common.cancel")}</AlertDialogCancel>
                            <AlertDialogAction
                              onClick={() => handleDelete(request.id)}
                              className="bg-destructive text-destructive-foreground hover:bg-destructive/90"
                            >
                              {t("merchant.delete")}
                            </AlertDialogAction>
                          </AlertDialogFooter>
                        </AlertDialogContent>
                      </AlertDialog>
                    )}
                  </div>
                </CardContent>
              </Card>
            );
          })}
        </div>
      )}
    </div>
  );
};

export default MerchantActivityRequests;
